body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  text-align: center;

  background-size: contain !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  height: 150px;
  width: 150px;
  border-radius: 2px;
  padding: 10px;
  margin: 10px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.board {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 450px;
  width: 570px;
  box-shadow: 4px 4px 10px gray;
}

.memory {
  user-select: none;
  width: 85%;
  background: #716F71;
  padding: 1rem;
  border-radius: 4px;
  box-shadow: 8px 9px 26px 0 rgba(46, 61, 73, 0.5);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin: 0 0 3em;
}

.memory > .card {
  font-size: 2em;
  flex: 1 1 calc(100% / 4 - 0.4em);
  outline: 0.08em solid silver;
  display: flex;
  height: 3.7rem;
  width: 3.7rem;
  margin: 0.2rem 0.2rem;
  background: #141214;;
  color: #ffffff;
  border-radius: 5px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 2px 20px 0 rgba(46, 61, 73, 0.5);
}

.memory > .card.hidden {
  background: silver;
}

.memory > .card.justMatched,
.memory > .card.guessed {
  outline: 0.1em solid green;
}
.memory > .card.wrong {
  outline-color: red;
}

.memory > .card.visible {
  cursor: not-allowed;
}

.memory > .card > .symbol {
  margin: auto;
}

.memory > .score{
  font-size: 4em;
  width: calc(100% - 0.4em);
  margin: 0.2em;
  text-align: center;
  font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
}

.hallOfFame {
  width: 100%;
  border-collapse: collapse;
}

.hallOfFame .score{
  width: 20%;
  text-align: center;
}